import React from 'react'
import { Link } from 'react-router-dom'
import { I18n } from 'react-i18nify-lite'
import { Fold,Group } from '../assets/stash'

const handleClick = (e)=>{
  if(window.Android){
    window.Android.givePermissions()
  } else{
    alert("ola oso")
  }
}

const initAdminActive = () => {
  if(window.Android){
    return window.Android.initAdminActive()
  } else {
    return false
  }
}
const initDrawOverlay = () => {
  if(window.Android){
    return window.Android.initDrawOverlay()
  } else {
    return false
  }
}
 
const initCamera = () => {
  if(window.Android){
    return window.Android.initCamera()
  } else {
    return false
  }
}
const initWriteStorage = () => {
  if(window.Android){
    return window.Android.initWriteStorage()
  } else {
    return false
  }
}
const initLocation = () => {
  if(window.Android){
    return window.Android.initLocation()
  } else {
    return true
  }
}
const initBackgroundLocation = () => {
  if(window.Android){
    return window.Android.initBackgroundLocation()
  } else {
    return false
  }
}
const initAndroid10OrAbove = () => {
  if(window.Android){
    return window.Android.initAndroid10OrAbove()
  } else {
    return false
  }
}


const initConfigure = () => {
  if(window.Android){
    return window.Android.initConfigure()
  } else {
    return true
  }
}

class Permission2 extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hasAdminActive: initAdminActive(),
      hasDrawOverlay: initDrawOverlay(),
      hasLocation: initLocation(),
      hasCamera: initCamera(),
      hasWriteStorage: initWriteStorage(),
      hasBackgroundLocation: initBackgroundLocation(),
      isAndroid10OrAbove: initAndroid10OrAbove(),
      hasConfigure: initConfigure(),
     
    }
  }


render() {
    return (
      <div className="security fs-onboarding regular inverted">
        <div className="column">
          <h4 className="heading">{I18n.t("permissions.title")}</h4>


          {
            (!this.state.hasLocation&&!this.state.isAndroid10OrAbove)||
            (!this.state.hasLocation&&this.state.isAndroid10OrAbove)||
            (this.state.hasLocation&&!this.state.hasBackgroundLocation&&this.state.isAndroid10OrAbove)||
            (!this.state.hasCamera)||
            (!this.state.hasWriteStorage) ? (


          <Fold
            initial="open"
            className="more-info"
            label={`
              ${I18n.t("permissions.category_permission")}
            `}
            button="fill transparent l default-text fold-trigger"
            >
            <Group>

          {
            !this.state.hasLocation&&!this.state.isAndroid10OrAbove ? (
                <div className="item">
                  <h5>{I18n.t("permissions.location.head")}</h5>
                  <p>{I18n.t("permissions.location.body")}</p>
                </div>
            ) :  null
          }
          {
            !this.state.hasLocation&&this.state.isAndroid10OrAbove ? (
                <div className="item">
                  <h5>{I18n.t("permissions.location.head")}</h5>
                  <p>{I18n.t("warning10.title")}</p>
                </div>
            ) :  null
          }
          {
            this.state.hasLocation&&!this.state.hasBackgroundLocation&&this.state.isAndroid10OrAbove ? (
                <div className="item">
                  <h5>{I18n.t("permissions.location.head")}</h5>
                  <p>{I18n.t("warning10.title")}</p>
                </div>
            ) :  null
          }
          {
            !this.state.hasCamera ? (
                <div className="item">
                  <h5>{I18n.t("permissions.camera.head")}</h5>
                  <p>{I18n.t("permissions.camera.body")}</p>
                </div>
            ) : null
          }
          {
            !this.state.hasWriteStorage ? (
                <div className="item">
                  <h5>{I18n.t("permissions.storage.head")}</h5>
                  <p>{I18n.t("permissions.storage.body")}</p>
                </div>
            ) : null
          }
          </Group>
          </Fold>
          ) :  null
          }
         
          {
            !this.state.hasAdminActive ? (
              <div>
               <div className="line-through"/> 
              <Fold
                initial="open"
                className="more-info"
                label={I18n.t("permissions.administrator_permission.head")}
                button="fill transparent l default-text fold-trigger"
              >
                <Group>
                    <div className="item">
                      <p>{I18n.t("permissions.administrator_permission.body")}</p>
                    </div>
              </Group>
              </Fold>
              </div>
          ) : null
          }
           
          {
            !this.state.hasDrawOverlay  ? (
               <div>
               <div className="line-through"/> 
              <Fold
                initial="open"
                className="more-info"
                label={`
                  ${I18n.t("permissions.over.head")}
                `}
                button="fill transparent l default-text fold-trigger"
              >

                <Group>
              {
                !this.state.hasDrawOverlay ? (
                    <div className="item">
                      <h5>{I18n.t("permissions.over.head")}</h5>
                      <p>{I18n.t("permissions.over.body")}</p>
                    </div>
                ) : null
              }
              </Group>
              
              </Fold>
              </div>
          ) : null
          }
           


        </div>
        <Group className="homeOptions">
          <Group>
            <Link to="#" onClick={handleClick} className="btn deafult fill">
              <span>{I18n.t("permissions.approve")}</span>
            </Link>
          </Group>
          {
            this.state.hasAdminActive&&
            this.state.hasLocation&&
            this.state.hasCamera&&
            this.state.hasWriteStorage? (
              <Group>
              {
                this.state.hasConfigure?(
                  <Link to={`/${I18n._locale}/`}  className="btn success fill">
                    <span>{I18n.t("permissions.next")}</span>
                  </Link>
                ):(
                  <Link to={`/${I18n._locale}/signin`}  className="btn success fill">
                  <span>{I18n.t("permissions.next")}</span>
                  </Link>
                )}
              </Group>
            ) :  null
          }
        </Group>
      </div>
    )
  }
}

export default Permission2