import React from "react";
import { Link } from "react-router-dom";
import { I18n } from "react-i18nify-lite";
import { Group } from "../assets/stash";
import loc from "../images/onboarding/ilust/05-Location.png";

const handleClickApprove = (e) => {
  if (window.Android) {
    window.Android.approveLocation();
  } else {
    alert("ola oso approve");
  }
};

const handleClickSkip = (e) => {
  if (window.Android) {
    window.Android.skipLocation();
  } else {
    alert("ola oso skip");
  }
};


class BgLoc extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="security fs-onboarding regular inverted">
        <div className="column">
          <h4 className="heading">
            {I18n.t("bgloc.title")}
          </h4>
          <p>
            {I18n.t("bgloc.body1")}
          </p>
          <p>
            {I18n.t("bgloc.body2")}
          </p>
          <p>
            {I18n.t("bgloc.body3")}
          </p>
          <img src={loc} className="first" alt="location" />
        </div>
        <Group className="homeOptions">
          <Group className="ctas">
            <Link to="#" onClick={handleClickSkip} className="btn alt flex">
              <span>{I18n.t("bgloc.skip")}</span>
            </Link>
            <Link to="#" onClick={handleClickApprove} className="btn flex">
              <span>{I18n.t("bgloc.approve")}</span>
            </Link>
          </Group>
        </Group>
      </div>
    );
  }
}

export default BgLoc;
